/* eslint-disable react/jsx-pascal-case */

import React from "react";
import { Button, Row, Col, Label } from "reactstrap";
import { Control, LocalForm, Errors } from "react-redux-form";

const required = (val) => val && val.length;
const maxLength = (len) => (val) => !val || val.length <= len;
const minLength = (len) => (val) => val && val.length >= len;
const isNumber = (val) => !isNaN(Number(val));
const validEmail = (val) =>
  /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);

function handleSubmit(values) {
  console.log("Current State is: " + JSON.stringify(values));
  alert("Current State is: " + JSON.stringify(values));
  // event.preventDefault();
}
export default function Contact() {
  return (
    <div className='row row-content bg-black'>
      <div className='col-12 text-center m-5'>
        <h3 className='text-light'>Send us your Feedback</h3>
      </div>
      <div className='col-12 col-md-9 ml-5'>
        <LocalForm
          onSubmit={(values) => handleSubmit(values)}
          className='d-block'
        >
          <Row className='form-group'>
            <Label htmlFor='firstname' md={2} className='text-light'>
              First Name
            </Label>
            <Col md={3}>
              <Control.text
                model='.firstname'
                id='firstname'
                name='firstname'
                placeholder='First Name'
                className='form-control'
                validators={{
                  required,
                  minLength: minLength(3),
                  maxLength: maxLength(15),
                }}
              />
              <Errors
                className='text-danger'
                model='.firstname'
                show='touched'
                messages={{
                  required: "Required",
                  minLength: " Must be greater than 2 characters",
                  maxLength: " Must be 15 characters or less",
                }}
              />
            </Col>
          </Row>
          <Row className='form-group'>
            <Label htmlFor='lastname' md={2} className='text-light'>
              Last Name
            </Label>
            <Col md={3}>
              <Control.text
                model='.lastname'
                id='lastname'
                name='lastname'
                placeholder='Last Name'
                className='form-control'
                validators={{
                  required,
                  minLength: minLength(3),
                  maxLength: maxLength(15),
                }}
              />
              <Errors
                className='text-danger'
                model='.lastname'
                show='touched'
                messages={{
                  required: "Required",
                  minLength: " Must be greater than 2 characters",
                  maxLength: " Must be 15 characters or less",
                }}
              />
            </Col>
          </Row>
          <Row className='form-group'>
            <Label htmlFor='telnum' md={2} className='text-light'>
              Contact Tel.
            </Label>
            <Col md={3}>
              <Control.text
                model='.telnum'
                id='telnum'
                name='telnum'
                placeholder='Tel. Number'
                className='form-control'
                validators={{
                  required,
                  minLength: minLength(3),
                  maxLength: maxLength(15),
                  isNumber,
                }}
              />
              <Errors
                className='text-danger'
                model='.telnum'
                show='touched'
                messages={{
                  required: "Required",
                  minLength: " Must be greater than 2 numbers",
                  maxLength: " Must be 15 numbers or less",
                  isNumber: " Must be a number",
                }}
              />
            </Col>
          </Row>
          <Row className='form-group'>
            <Label htmlFor='email' md={2} className='text-light'>
              Email
            </Label>
            <Col md={10}>
              <Control.text
                model='.email'
                id='email'
                name='email'
                placeholder='Email'
                className='form-control'
                validators={{
                  required,
                  validEmail,
                }}
              />
              <Errors
                className='text-danger'
                model='.email'
                show='touched'
                messages={{
                  required: "Required",
                  validEmail: " Invalid Email Address",
                }}
              />
            </Col>
          </Row>
          <Row className='form-group'>
            <Col md={{ size: 6, offset: 2 }}>
              <div className='form-check'>
                <Label check>
                  <Control.checkbox
                    model='.agree'
                    name='agree'
                    className='form-check-input'
                  />{" "}
                  <strong className='text-light'>May we contact you?</strong>
                </Label>
              </div>
            </Col>
            <Col md={{ size: 3, offset: 1 }}>
              <Control.select
                model='.contactType'
                name='contactType'
                className='form-control'
              >
                <option>Tel.</option>
                <option>Email</option>
              </Control.select>
            </Col>
          </Row>
          <Row className='form-group'>
            <Label htmlFor='message' md={2} className='text-light'>
              Your Feedback
            </Label>
            <Col md={10}>
              <Control.textarea
                model='.message'
                id='message'
                name='message'
                rows='12'
                className='form-control'
              />
            </Col>
          </Row>
          <Row className='form-group'>
            <Col md={{ size: 10, offset: 2 }}>
              <Button type='submit' color='primary' className='text-light'>
                Send Feedback
              </Button>
            </Col>
          </Row>
        </LocalForm>
      </div>
    </div>
  );
}
